@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: kalameh;
  src: url(../public/fonts/Kalameh-Regular.ttf);
}

html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

@media only screen and (max-width: 760px) {
  body nav {
    right: -200px;
    margin-top: -50px;
    margin-right: 5px;
  }

  body .display-page {
    margin-right: 0;
    padding: 10px;
  }

  body .drp-btn {
    display: block;
    margin-right: 10px;
  }

  body #welcome {
    margin-top: -30px;
  }

  body .page {
    display: block;
  }

  body .home {
    margin-top: 50px;
    direction: rtl;
  }

  body .logo {
    width: 150px;
    margin-top: 150px;
  }

  body .home-textbox {
    margin-top: -160px;
    text-align: center;
  }

  body .titles {
    width: 90%;
  }

  body #first-homebox {
    margin-top: 100px;
  }

  body #text-1 {
    padding-right: 10%;
  }
  
  body #text-2 {
    padding-right: 10%;
    padding-left: 10px;
  }

  body #welcome-btn {
    margin-right: 40px;
  }
}

@media only screen and (min-width: 770px) {
  body nav {
    right: 0;
    margin-top: -50px;
  }
}

@media only screen and (max-width: 1230px) {
  body .service-template {
    width: 500px;
  }

  body .clean-code {
    flex-wrap: wrap;
  }

  body .Demo-temp {
    width: 400px;
  }

  body #demoshow {
    flex-wrap: wrap;
  }

  body .contact-temp {
    width: 400px;
  }
}

@keyframes load {
  0% {
    top: 30px;
    opacity: 0;
  } 100% {
    opacity: 1;
    top: 0;
  }
}

@keyframes logomove {
  0% {
    top: 0;
  } 50% {top: 20px;}
  100% {
    top: 0;
  }
}

::-webkit-scrollbar {
  width: 7px;
  background-color: #292929;
}

::-webkit-scrollbar-thumb {
  background-color: #717171;
  border-radius: 30px;
}

:root {
  --color1: #fff;
  --color2: #b8b8b8;
  --color3: rgb(88, 202, 255);
  --color4: #292929;
}


body {
  background-color: var(--color1);
  padding: 0px;
  font-family: kalameh;
  height: 100%;
  transition: 200ms;
  z-index: 100;
  overflow-x: hidden;
}

.display-page {
  transition: 200ms;
  position: absolute;
  z-index: 1;
  top: 20px;
  width: 100%;
  margin-right: 4.3%;
  padding: 10px;
}

.btn-01 {
  width: 100px;
  height: 50px;
  border: 2px solid var(--color3);
  background-color: var(--color3);
  color: #000;
  border-radius: 10px;
  cursor: pointer;
  transition: 200ms;
  font-size: 20px;
}

.btn-01:hover {
  background-color: inherit;
  color: var(--color3);
}

.btn-showall {
  width: 50px;
  height: 50px;
  border: 2px solid var(--color3);
  background-color: var(--color3);
  color: #000;
  border-radius: 10px;
  cursor: pointer;
  transition: 200ms;
}

.btn-showall:hover {
  background-color: inherit;
  color: var(--color3);
}

nav {
  background-color: #ffffff00;
  width: 95px;
  height: 97%;
  box-shadow: 2px 2px 20px #00000014;
  display: flex;
  justify-content: center;
  border-radius: 15px;
  padding: 5px;
  overflow: hidden;
  position: fixed;
  transition: 200ms;
  background-color: #fff;
  z-index: 100;
  top: 60px;
  backdrop-filter: blur(10px);
  margin-right: 10px;
}

.navigation {
  display: block;
  list-style-type: none;
  margin-right: -3px;
  margin-top: 2px;
}

.navigation button {
  width: 80px;
  height: 80px;
  border-radius: 8px;
  transition: 200ms;
  cursor: pointer;
  color: var(--color2);
  font-size: 13px;
}


.navigation button i {
  font-size: 15px;
}

.navigation button:hover {
  background-color: #ededed37;
}


.pointer {
  width: 30px;
  height: 30px;
  background-color: var(--color3);
  color: var(--color3);
  border-radius: 10px;
  position: relative;
  top: 25px;
  transition: 200ms;
}

#dark:hover {
  color: var(--color3);
}

#light:hover {
  color: yellow;
}

.drp-btn {
  width: 50px;
  height: 50px;
  box-shadow: 2px 2px 20px #00000022;
  justify-content: center;
  color: var(--color3);
  display: none;
  border: 2px solid #f3f3f30a;
  border-radius: 15px;
  z-index: 99;
  position: fixed;
  top: 10px;
  backdrop-filter: blur(20px);
}

.box-title {
  font-size: 40px;
}


.service-template {
  width: 32%;
  height: 250px;
  box-shadow: 2px 2px 20px #0000000a;
  border-radius: 10px;
  border: 2px solid #00000000;
  transition: 200ms;
  padding: 20px;
  border: 2px solid #ffffff0a;
  cursor: default;
}

.service-template:hover { 
  transform: translateY(-10px);
  border: 2px solid #ffffff03;
}

.service-template:hover .service-icon {
  background-color: inherit;
  color: var(--color3);
  border-radius: 20%;
}

.service-icon {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: var(--color3);
  color: inherit;
  cursor: default;
  transition: 200ms;
  overflow: hidden;
}

.load {
  animation: load 1s;
  position: relative;
}

.border-text {
  width: 150px;
  height: 4px;
  background-color: var(--color3);
  margin-top: 10px;
}

.Demo-temp {
  width: 33%;
  height: auto;
  box-shadow: 2px 2px 30px #0000000f;
  border-radius: 15px;
  cursor: pointer;
  transition: 200ms;
  border: 2px solid #ffffff0a;
  overflow: hidden;
  padding: 10px;
  overflow: hidden;
}

.Demo-temp:hover {
  transform: translateY(-5px);
}

.demo-tech {
  width: auto;
  background-color: rgb(199, 237, 255);
  text-align: center;
  color: rgb(69, 111, 131);
  padding: 5px;
  border-radius: 5px;
  font-size: 13px;
  transition: 200ms;
}

.demo-tech:hover {
  background-color: rgb(163, 223, 252);
}

.demo-tech-2 {
  width: auto;
  background-color: rgb(199, 237, 255);
  text-align: center;
  color: rgb(69, 111, 131);
  padding: 5px;
  border-radius: 5px;
  font-size: 16px;
  transition: 200ms;
}

.contact-temp {
  width: 31%;
  height: 95px;
  box-shadow: 2px 2px 20px #0000000b;
  border-radius: 10px;
  border: 2px solid #ffffff0f;
  transition: 200ms;
  background-color: inherit;
  padding-right: 30px;
  cursor: pointer;
}

.contact-temp:hover {
  box-shadow: 2px 2px 20px #00000014;
  transform: translateY(-5px);
}

.contact-img {
  width: 50px;
  height: 50px;
  border-radius: 10px;
  background-color: var(--color3);
  position: relative;
  right: -10px;
  margin-top: 20px;
  cursor: default;
  font-size: 20px;
}

.con-title {
  transition: 200ms;
}

.contact-temp:hover .con-title {
  color: var(--color3);
  border-bottom: 3px solid inherit;
}


